import { Component, OnInit } from '@angular/core';
import { User } from '../../user';
import { Router } from '@angular/router';
import { UserService } from '../../shared_service/user.service';
import { Http,Response,Headers} from '@angular/http';
import { empty } from 'rxjs';

@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html',
  styleUrls: ['./user-form.component.css']
})
export class UserFormComponent implements OnInit {
  netImage:any = "../assets/headerLtImg3.png";
  private user:User;
  constructor(private _userService:UserService,private _router:Router,private http: Http) { }

  addUser()
  {
	  /*changed id=empCode*/
    if(this.user.empCode==undefined)
    {
      this._userService.updateUserDetails(this.user).subscribe((user)=>{
        console.log(user);
        this._router.navigate(['/']);
      });
    }else{
      this._userService.SaveUserDetails(this.user).subscribe((user)=>{
        console.log("Details:",user);
        this._router.navigate(['/']);
      });
    }
  }

  

  ngOnInit() {
    this.user=this._userService.getter();
  }

}
