import { Injectable } from '@angular/core';
import { Http,Response,Headers, RequestOptions } from '@angular/http';
import { User } from '../user';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  /* private baseUrl:string='http:localhost:8085'; */
  private headers =  new Headers({'Content-Type':'application/json'});
  // private headers =  new Headers({'Content-Type':'application/json','Authorization':'Bearer ..ZOI8CvMZNdMbGYlawtMcQ_qlEvl_gsxP7BsNaNU9sZnht027OVf_CiRCdvfv4DOOLbitjnVcD-byIGX19sXJ3M4aQn9vqtrC2HowRqFB9mqrdxBLxx4pviyE0S5An2C4746tv0WWkZzRo42r9L7gUtEAxqEPisu86H31EAFDqVD1NuPIpiFJmdSlbgLLCU4pwwqs6COGUCDj0xtZTJ8FALUcz-kcYWpBH19c6C9V2XCtZs5LCoZkRk_B15KtDdc7dlphSc0XpaWKVql6p5Kjl5bwmGva1waex09BQGEshBYKjEvN3ZR2GDAKxBxdyyYHN3AGT2J4ONzIZXzcJkkFIA'});
  private options = new RequestOptions({headers:this.headers});
  private user:User;
  constructor(private _http:Http) { }
  
  //  getAzureData()
  //  {
  //    return this._http.get("https://graph.microsoft.com/v1.0/users",this.options);

  //  }

  getAllUserDetails()
  {
    return this._http.get("/getAllUserDetails",this.options);
  }

  deleteUserDetails(empCode:Number)
  {
    return this._http.delete("/deleteUserDetails?empCode="+empCode,this.options);
  }

  SaveUserDetails(user:User)
  {
    return this._http.post("/SaveUserDetails",JSON.stringify(user),this.options);
  }

  updateUserDetails(user:User)
  {
    return this._http.put("/updateUserDetails?empCode="+user.empCode,JSON.stringify(user),this.options);
  }

  setter(user:User){
    this.user=user;
  }
  getter()
  {
    return this.user;
  }
}
